import React from 'react'
import { Pagination, PaginationItem, PaginationLink } from 'reactstrap';

const PaginationPanel = props => {
  let withNumbersQuantity = props.buttons.withNumbersQuantity || 5
  if (withNumbersQuantity > props.pagesQuantity) withNumbersQuantity = props.pagesQuantity

  const different = Math.floor(withNumbersQuantity / 2)
  let firstButton = props.currentPage - different
  if (firstButton < 0) firstButton = 0
  if (firstButton + withNumbersQuantity > props.pagesQuantity) firstButton = props.pagesQuantity - withNumbersQuantity

  const buttonsToShow = []
  for (let i = 0; i < withNumbersQuantity; i++) {
    buttonsToShow.push(firstButton + 1 + i)
  }

  return (
    <div style={{ marginTop: 30 }} {...props.paginationDiv}>

      <Pagination>
        {props.buttons.first &&
          <PaginationItem
            disabled={props.currentPage === 0}
            onClick={() => props.changePage(0)}
          >
            <PaginationLink first tag="button" />
          </PaginationItem>
        }
        {props.buttons.previous &&
          <PaginationItem
            disabled={props.currentPage === 0}
            onClick={() => props.changePage(props.currentPage - 1)}
          >
            <PaginationLink previous tag="button" />
          </PaginationItem>
        }
        {buttonsToShow.map(el => (
          <PaginationItem
            active={el - 1 === props.currentPage}
            onClick={() => props.changePage(el - 1)}
            key={el}
          >
            <PaginationLink tag="button">
              {el}
            </PaginationLink>
          </PaginationItem>
        ))}

        {props.buttons.next &&
          <PaginationItem
            disabled={props.currentPage === props.pagesQuantity - 1}
            onClick={() => props.changePage(props.currentPage + 1)}
          >
            <PaginationLink next tag="button" />
          </PaginationItem>
        }
        {props.buttons.last &&
          <PaginationItem
            disabled={props.currentPage === props.pagesQuantity - 1}
            onClick={() => props.changePage(props.pagesQuantity - 1)}
          >
            <PaginationLink last tag="button" />
          </PaginationItem>
        }
      </Pagination>
    </div>
  )
}

export default PaginationPanel