import './Table.css'
import React from 'react'

import PaginationPanel from './PaginationPanel'

class Table extends React.Component {
  constructor(props) {
    super()

    const columns = this.mapColumns(props.columns)
    const subColumns = this.mapSubColumns(props.columns)
    const keys = this.setKeys(props.columns)
    const data = props.pagination ? this.divideData(props.data, props.pagination.elementsPerPage) : [props.data]

    this.state = {
      columns,
      subColumns,
      keys,
      data,
      pagination: !!props.pagination,
      pagesQuantity: data.length,
      currentPage: props.pagination ? (props.pagination.startPage || 0) : 0,
    }
  }

  mapColumns(columns) {
    return columns.map(el => (
      {
        title: el.title,
        key: el.key,
        rows: el.columns ? 1 : 2,
        columns: el.columns ? el.columns.length : 1,
        columnProps: el.columnProps
      }
    ))
  }

  mapSubColumns(columns) {
    return columns.reduce((red, el) => {
      if (el.columns) {
        return [...red, ...el.columns]
      }
      return red
    }, [])
  }

  setKeys(columns) {
    return columns.reduce((red, el) => {
      if (el.columns) {
        const secondKeys = el.columns.map(el => ({ value: el.key, formatter: el.formatter, columnProps: el.columnProps }))
        return [...red, ...secondKeys]
      }
      return [...red, { value: el.key, formatter: el.formatter, columnProps: el.columnProps }]
    }, [])
  }

  divideData(data, length = 10) {
    return data.reduce((red, el) => {
      if (red[red.length - 1].length >= length) {
        return [...red, [el]]
      }
      red[red.length - 1].push(el)
      return red
    }, [[]])
  }

  componentDidUpdate(prevProps) {
    if (
      prevProps.columns !== this.props.columns ||
      prevProps.data !== this.props.data ||
      prevProps.columns.length !== this.props.columns.length ||
      prevProps.data.length !== this.props.data.length ||
      prevProps.data[prevProps.data.length - 1] !== this.props.data[this.props.data.length - 1]
    ) {
      const columns = this.mapColumns(this.props.columns)
      const subColumns = this.mapSubColumns(this.props.columns)
      const keys = this.setKeys(this.props.columns)
      const data = this.props.pagination ? this.divideData(this.props.data, this.props.pagination.elementsPerPage) : [this.props.data]

      this.setState({
        columns,
        subColumns,
        keys,
        data,
        pagination: !!this.props.pagination,
        pagesQuantity: data.length,
        currentPage: this.props.pagination ? (this.props.pagination.startPage || 0) : 0
      })
    }

  }

  changePage = (page = 0) => {
    if (page < 0 || page > this.state.pagesQuantity - 1) return
    if (this.props.pagination && typeof this.props.pagination.callback === 'function') this.props.pagination.callback(page)
    this.setState({ currentPage: page })
  }


  render() {
    const { table, thead, tbody, tr, td, th, div, paginationDiv, pagination } = this.props
    const dataToShow = this.state.data[this.state.currentPage]

    return (
      <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }} {...div}>
        <table {...table}>
          <thead {...thead}>
            <tr {...tr}>
              {this.state.columns.map(el => {
                return <th
                  key={el.key || el.title}
                  {...th}
                  {...el.columnProps}
                  {...el.props}
                  style={Object.assign((el.rows === 1 ? { borderBottom: 'none', textAlign: 'center' } : {}), th && th.style, el.columnProps && el.columnProps.style, el.props && el.props.style)}
                  rowSpan={el.rows}
                  colSpan={el.columns}
                >
                  {el.title}
                </th>
              })}
            </tr>
            {!!this.state.subColumns.length &&
              <tr {...tr}>
                {this.state.subColumns.map(el => (
                  <th
                    key={el.key || el.title}
                    {...th}
                    {...el.columnProps}
                    {...el.props}
                    style={Object.assign({}, th && th.style, el.columnProps && el.columnProps.style, el.props && el.props.style)}
                  >
                    {el.title}
                  </th>
                ))}
              </tr>
            }
          </thead>
          <tbody {...tbody}>
            {dataToShow.map((el, i) => (
              el.row ||
              <tr
                key={'column-' + i}
                {...tr}
                {...el.props}
                {...Object.assign({}, this.props.cyclic && i % this.props.cyclic.number === 0 && this.props.cyclic.props)}
                style={Object.assign({}, tr && tr.styles, this.props.cyclic && i % this.props.cyclic.number === 0 && this.props.cyclic.props && this.props.cyclic.props.style, el.props && el.props.style)}
              >
                {this.state.keys.map(key => (
                  <td
                    key={key.value + '-' + i}
                    {...td}
                    {...key.columnProps}
                    {...el[key.value + 'Props']}
                    style={Object.assign({}, td && td.styles, key.columnProps && key.columnProps.style, el[key.value + 'Props'] && el[key.value + 'Props'].style)}
                  >
                    {key.formatter ? key.formatter(el[key.value]) : el[key.value]}
                  </td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
        {this.state.pagination && this.state.pagesQuantity > 1 &&
          <PaginationPanel
            paginationDiv={paginationDiv}
            pagesQuantity={this.state.pagesQuantity}
            currentPage={this.state.currentPage}
            changePage={this.changePage}
            {...pagination}
          />}
      </div>
    )
  }
}

export default Table