import React from 'react'

import { Button } from '@material-ui/core'
import Info from './Info'
import Table from './components/Table'
import Accessibility from '@material-ui/icons/Accessibility'

import './main.css'

const columns = [
  { key: 'default', title: 'zwykła (columnProps)', columnProps: { style: { backgroundColor: 'cyan', textAlign: 'center' } } },
  {
    key: 'divided',
    title: 'dzielona',
    columns: [
      { key: 'first', title: 'z formatterem', formatter: (value) => <div>{value} + format</div> },
      { key: 'second', title: 'z propsami', props: { style: { backgroundColor: 'red', fontWeight: 'bold', color: 'green', fontSize: 20 } } }
    ]
  },
  { key: 'component', title: <Button color='primary' variant='contained' onClick={() => alert('click')}> Komponent</Button> }
]

const data = [
  {
    default: 'click me',
    first: 'click me',
    second: 'click me',
    component: 'click me',
    props: { onClick: () => alert('funkcja w propsach dla całego wiersza'), style: { cursor: 'pointer' } }
  },
  {
    default: <div><Accessibility /> ikona</div>,
    first: <span
      style={{ cursor: 'pointer', fontSize: 30, userSelect: 'none' }}
      className='table-example__component--click'
      onClick={() => {
        document.querySelector('.table-example__component--click').style.color = `rgb(${Math.random() * 255},${Math.random() * 255},${Math.random() * 255})`
      }}
    >
      click!
    </span>,
    second: `propsy dla pojedyńczego okienka [key+'Props']`,
    secondProps: { style: { fontStyle: 'italic', backgroundColor: 'grey', maxWidth: 120, color: 'black' } },
    component:
      <div
        style={{ width: 140, position: 'relative' }}
      >
        interwał
        <span
          style={{ transition: '1s', width: 20, height: 20, backgroundColor: 'green', borderRadius: '100%', position: 'absolute', left: 0 }}
          className='table--example__span--interval'
        />
      </div>,
  },
  {
    row: <tr key='mojwlasny'><td colSpan={4} style={{ textAlign: 'center' }}> cały wiersz, uzyskałem taki efekt dodając do tablicy data kolejny obiekt z właściwością:<br />
      {`row: <tr key={jak nie podasz klucza - react nakrzyczy}><td colSpan={4} style={{ textAlign: 'center' }}>{TEKST}</td></tr>`}<br />
      jeżeli chcesz zachować efekt tabelki musisz dodać {'<tr> ...odpowiednią ilość td lub używać colSpan tak aby była zachowana ilość kolumn</tr>'}
    </td></tr>
  }
]

const paginateColumns = [
  { title: 'Liczba', key: 'number' },
  { title: 'Potęga', key: 'pow' },
  { title: 'Pierwiastek', key: 'sqrt' }
]

const paginateData = []
for (let i = 1; i <= 100; i++) {
  paginateData.push({
    number: i,
    pow: i * i,
    sqrt: Math.sqrt(i).toFixed(2)
  })
}

class TableExample extends React.Component {
  componentDidMount() {
    const span = document.querySelector('.table--example__span--interval')
    this.interval = setInterval(() => {
      const x = +span.style.left.slice(0, -2)
      span.style.left = (x === 0 ? '100px' : '0px')
    }, 1000)
  }

  componentWillUnmount() {
    clearInterval(this.interval)
  }

  render() {
    return (
      <div style={{ maxWidth: 1400, margin: 'auto' }}>
        <h1 style={{ textAlign: 'center' }}>Component Table </h1>
        <Info />
        <div style={{ padding: 30, backgroundColor: 'white' }}>
          <Table columns={columns} data={data} />
          <br />
          <h2 style={{ textAlign: 'center' }}>Paginacja</h2>
          <Table
            columns={paginateColumns}
            data={paginateData}
            pagination={{
              elementsPerPage: 5,
              startPage: 0,
              callback: currentPage => console.log(currentPage),
              buttons: {
                first: true,
                last: true,
                next: true,
                previous: true,
                withNumbersQuantity: 5
              }
            }
            }
            cyclic={{
              number: 2,
              props: {
                style: {
                  backgroundColor: 'rgba(130,130,130,0.1)'
                }
              }
            }
            }
          />

        </div>
      </div>
    )
  }
}


export default TableExample